* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

img {
  display: flex;
  width: 100%;
  height: auto;
}

form {
  position: relative;
  width: 450px;
  margin: 0 auto;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 15px;
  row-gap: 15px;
  column-gap: 15px;
}

.gallery .photo-card {
  display: inline-block;
  width: 400px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.gallery .photo-card:hover {
  transform: scale(1.03);
}

.photo-card img {
  width: 400px;
  height: 240px;
}

.form-wrap {
  padding: 15px 0;
  background-color: #00BFFF;
}

.form-wrap input {
    width: 100%;
    height: 42px;
    padding-left: 10px;
    border: 2px solid #20B2AA;
    border-radius: 5px;
    outline: none;
    background: #F9F0DA;
    color: #000000;
  }
  .form-wrap button {
    position: absolute; 
    top: 0;
    right: 0px;
    width: 58px;
    height: 42px;
    border: none;
    background: #20B2AA;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
  }
  .form-wrap button:before {

    content: "Search";
    font-family: FontAwesome;
    font-size: 16px;
    color: #F9F0DA;
  }

  .load-more {
    font-family: FontAwesome;
    display: block;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 30px;
    color: #F9F0DA;
    width: 100px;
    height: 42px;
    border: none;
    background: #20B2AA;
    border-radius: 5px 5px 5px 5px;
    cursor: pointer;
  }

.photo-card .info {
  display: flex;
  justify-content: space-around;
}

.info-item {
  text-align: center;
}